<template>
  <div class="container">
    <div v-html="apply">
    </div>
  </div>
</template>
<script>
export default {
  data(){
      return {
        apply: '',
        path: '',
        WsUrl: process.env.WsUrl,
        orderNum: this.$route.query.orderNum,
        socket: ""
      }
  },
  mounted(){
    let me = this
    //支付宝返回表单前端调用页面
    let form = localStorage.getItem('html')
    me.apply = form;
    me.$nextTick(()=>{
      document.forms[0].submit()
    })
    
    if (me.socket && me.socket.onclose) {
      me.socket.onclose = me.close()
    }
    // 初始化
    me.path = me.WsUrl + me.orderNum + '?Authorization=Bearer ' + localStorage.getItem('token')
    console.log("URL======>" + me.path)
    me.init()
  },
  methods:{
    init: function () {
      if (typeof(WebSocket) === "undefined") {
        alert("您的浏览器不支持socket")
      } else {
        // 实例化socket
        this.socket = new WebSocket(this.path)
        // 监听socket连接
        this.socket.onopen = this.open
        // 监听socket错误信息
        this.socket.onerror = this.error
        // 监听socket消息
        this.socket.onmessage = this.getMessage
      }
    },
    open: function () {
      console.log("socket连接成功")
    },
    error: function () {
      console.log("连接错误")
    },
    getMessage: function (msg) {
      console.log("1",msg.data)
      console.log("2",msg)
      if (msg.data.indexOf('orderSn') !== -1 && JSON.parse(msg.data).payStatus === 'success') {
        this.$router.push({
          path:'/user/paySuccess',
          query: {
            orderNum: JSON.parse(msg.data).orderSn
          }
        })
      }
    },
    send: function () {
      this.socket.send()
    },
    close: function () {
      console.log("socket已经关闭")
    }
  },
  destroyed () {
    // 销毁监听
    if (this.socket && this.socket.onclose) {
      this.socket.onclose = this.close()
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/styles/variable.styl';
  .wxtip {
  background: rgba(0, 0, 0, 0.8);
  text-align: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  img {
    width: 100%;
  }
}
.wxtip-txt {
  margin-top: 107px;
  color: #fff;
  font-size: 20px;
  line-height: 1.8;
}
</style>


